export default function Topbar_Gobernador(props) {

    return (
        <nav class="navbar navbar-expand navbar-light bg-white topbar mb-4 static-top shadow">

            {/* Sidebar Toggle (Topbar) */}
            <button id="sidebarToggleTop" class="btn btn-link d-md-none rounded-circle mr-3">
                <i class="fa fa-bars"></i>
            </button>

            {/* Topbar Navbar */}
            <ul class="navbar-nav">

                {/* Nav Item - Caballos */}
                <li class="nav-item">
                    <div class="nav-link">
                        <img class="nav-item icono oscuro" alt="caballos"
                            src="data:image/svg+xml;base64," />
                    </div>
                </li>
                <li class="nav-item">
                    <div class="nav-link">
                        <i class="nav-item oscuro fas fa-3x">{props.recursos.caballos}</i>
                    </div>
                </li>

                {/* Divider */}
                <div class="topbar-divider d-none d-sm-block"></div>

                {/* Nav Item - Vacas */}
                <li class="nav-item">
                    <div class="nav-link">
                        <img class="nav-item icono oscuro" alt="vacas"
                            src="data:image/svg+xml;base64," />
                    </div>
                </li>
                <li class="nav-item">
                    <div class="nav-link">
                        <i class="nav-item oscuro fas fa-3x">{props.recursos.vacas}</i>
                    </div>
                </li>

                {/* Divider */}
                <div class="topbar-divider d-none d-sm-block"></div>

                {/* Nav Item - Hierro */}
                <li class="nav-item">
                    <div class="nav-link">
                        <img class="nav-item icono oscuro" alt="hierro"
                            src="data:image/svg+xml;base64," />
                    </div>
                </li>
                <li class="nav-item">
                    <div class="nav-link">
                        <i class="nav-item oscuro fas fa-3x">{props.recursos.hierro}</i>
                    </div>
                </li>

                {/* Divider */}
                <div class="topbar-divider d-none d-sm-block"></div>

                {/* Nav Item - Vino */}
                <li class="nav-item">
                    <div class="nav-link">
                        <img class="nav-item icono oscuro" alt="vino"
                            src="data:image/svg+xml;base64," />
                    </div>
                </li>
                <li class="nav-item">
                    <div class="nav-link">
                        <i class="nav-item oscuro fas fa-3x">{props.recursos.vino}</i>
                    </div>
                </li>

                {/* Divider */}
                <div class="topbar-divider d-none d-sm-block"></div>

                {/* Nav Item - Yerba */}
                <li class="nav-item">
                    <div class="nav-link">
                        <img class="nav-item icono oscuro" alt="yerba"
                            src="data:image/svg+xml;base64," />
                    </div>
                </li>
                <li class="nav-item mr-5">
                    <div class="nav-link">
                        <i class="nav-item oscuro fas fa-3x">{props.recursos.yerba}</i>
                    </div>
                </li>

                {/* Divider */}
                <div class="topbar-divider d-none d-sm-block ml-5"></div>

                <div class="titulo">
                    <span class="tituloIndependencia">Independencia:</span> <span class="tituloElMegajuego"> el Megajuego</span>
                </div>

            </ul>

        </nav>
    )
}